<template>
  <div id="page-content" class="header-static">
    <div class="container">
      <div class="row padding-onlytop-lg">
        <div class="col-md-8 center col-md-offset-2">
          <div
            class="time-screen"
            v-bind:class="{ green: status == 'RESTING','time-screen-wechat':userAgent.includes('WeChat') }"
            v-show="status == 'WORKING' || status == 'RESTING'"
          >
            <span class="time-item" v-text="hour"></span>
            <span>:</span>
            <span class="time-item" v-text="minute"></span>
            <span>:</span>
            <span class="time-item" v-text="second"></span>
          </div>
          <form id="contact-form">
            <div class="submit-area padding-sm">
              <input
                v-show="status == 'READY_WORK'"
                v-on:click="startWorking"
                class="btn-alt active ready-work-btn"
                type="button"
                value="开始集中精力"
              />
              <input
                v-show="status == 'READY_REST'"
                v-on:click="startRest"
                class="btn-alt active ready-rest-btn"
                type="button"
                value="开始休息"
              />
              <input
                v-show="status == 'WORKING' || status == 'RESTING'"
                v-on:click="start"
                class="btn-alt active"
                type="button"
                value="开始/暂停"
              />
              <input
                v-show="status == 'WORKING' || status == 'RESTING'"
                v-on:click="init"
                class="btn-alt btn-danger"
                type="button"
                value="重新开始"
              />
              <input
                v-show="status == 'WORKING' || status == 'RESTING'"
                v-on:click="zero"
                class="btn-alt btn-danger"
                type="button"
                value="快速归零"
              />
            </div>
            <div class="submit-area padding-sm" v-show="false">
              <input v-on:click="start" class="btn-alt active" type="button" value="开始/暂停" />
              <input v-on:click="record" class="btn-alt active" type="button" value="记录" />
              <input v-on:click="stop" class="btn-alt btn-danger" type="button" value="清除" />
              <div id="msg" class="message"></div>
            </div>
          </form>
          <div class="margin-bottom record" :key="index" v-for="(item,index) in recorded">{{item}}</div>
        </div>
      </div>
    </div>
  </div>
</template>
 
<script>
export default {
  data: function () {
    return {
      hour: '00',
      minute: '00',
      second: '00',
      running: false,
      timer: {},
      recorded: [],
      lastTimeMill: 0,
      timeDate: '',
      status: 'READY_WORK', //WORKING,READY_REST,RESTING
      interval: 25,
      circle: 0,
      userAgent: navigator.userAgent,
    }
  },
  mounted: function () {},
  methods: {
    init: function () {
      this.stop()
      this.status = 'READY_WORK'
    },
    zero: function () {
      if (this.timeDate) {
        this.timeDate = new Date(0)
      }
    },
    startWorking: function () {
      /* 开始工作，倒计时25分钟 */
      this.interval = 25
      this.start()
      this.status = 'WORKING'
    },
    startRest: function () {
      /* 开始休息，倒计时5分钟 */
      if (this.circle % 4 == 0) {
        this.interval = 15
      } else {
        this.interval = 5
      }
      this.start()
      this.status = 'RESTING'
    },
    start: function () {
      if (this.running) {
        this.running = false
        //从时间0点开始的毫秒值记录为上一次暂停的时间
        this.lastTimeMill = this.timeDate.getTime()
        clearInterval(this.timer)
        return
      }
      this.running = true
      var vm = this
      if (this.interval && this.lastTimeMill == 0) {
        var startTimePoint = new Date().getTime() + this.interval * 60 * 1000
      } else {
        var startTimePoint = new Date().getTime()
      }
      let runner = function () {
        if (
          (vm.status == 'WORKING' || vm.status == 'RESTING') &&
          vm.timeDate &&
          vm.timeDate.getTime() <= 0
        ) {
          vm.stop()
        }
        //从时间0点开始的日期
        if (vm.interval) {
          vm.timeDate = new Date(
            startTimePoint - new Date().getTime() + vm.lastTimeMill
          )
        } else {
          vm.timeDate = new Date(
            new Date().getTime() - startTimePoint + vm.lastTimeMill
          )
        }

        //格式化
        vm.hour = ('0' + vm.timeDate.getUTCHours()).slice(-2)
        vm.minute = ('0' + vm.timeDate.getUTCMinutes()).slice(-2)
        vm.second = ('0' + vm.timeDate.getUTCSeconds()).slice(-2)
        return runner
      }
      //runner()带括号表示立即执行一次
      this.timer = setInterval(runner(), 1000)
    },
    stop: function () {
      this.running = false
      clearInterval(this.timer)
      this.minute = '00'
      this.second = '00'
      this.hour = '00'
      this.millsecond = '000'
      this.lastTimeMill = 0
      if (this.status == 'WORKING') {
        this.circle++
        this.record()
        this.status = 'READY_REST'
      }
      if (this.status == 'RESTING') {
        this.status = 'READY_WORK'
      }
    },
    record: function () {
      this.recorded.push('已完成' + this.circle + '个番茄周期')
    },
  },
}
</script>
<style scoped>
.time-screen {
  height: 178px;
  font-size: 52px;
  padding: 15px;
  color: var(--main-color);
  text-shadow: 8px 8px 17px rgba(0, 0, 0, 0.3);
  min-width: none;
}
.time-screen-wechat {
  font-size: 26px !important;
}
.btn-alt {
  display: block;
  margin: auto;
  margin-top: 20px;
}
.time-item {
  display: inline-block;
  width: 70px;
  vertical-align: middle;
}
.time-item-long {
  display: inline-block;
  width: 100px;
  vertical-align: middle;
}
.record {
  font-size: 33px;
  margin-bottom: 8px;
}

.ready-work-btn {
}

.ready-rest-btn {
  background-color: rgba(41, 184, 64, 0.884);
}
.green {
  color: rgba(41, 184, 64, 0.884);
}
@media (min-width: 768px) {
  .time-screen {
    height: 194px;
    font-size: 123px;
    min-width: 892px;
  }
  .btn-alt {
    display: inline;
    margin: 13px;
  }
  .time-item {
    width: 167px;
  }
  .time-item-long {
    width: 230px;
  }
}
</style>

